import service from '../../service.js';
import { mapState, mapMutations } from 'vuex';
import mInput from '../../components/m-input.vue';

export default {
	components: {
		mInput
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0,
			isDevtools: false,
			userName: '3',
			userPass: '111'
		};
	},
	onShow() {
		this.$store.commit('GetNetWork');
	},
	computed: mapState(['forcedLogin', 'api', 'userGuid']),
	methods: {
		// ...mapMutations(['login']),
		login() {
			console.log(this.api);
			const _this = this;
			const UserGuid = uni.getStorageSync('UserGuid');
			const UserEmpID = uni.getStorageSync('UserEmpID');
			// uni.request({
			// 	url: this.$store.state.api + 'login',
			// 	method: 'POST',
			// 	data: {
			// 		username: this.userName,
			// 		userpass: this.userPass
			// 	},
			// 	header:{
			// 		'token': JSON.stringify({
			// 			UserGuid: UserGuid,
			// 			UserEmpID: UserEmpID
			// 		}),
			// 		'Content-Type': 'application/x-www-form-urlencoded'
			// 	},
			// 	success(data) {
			// 		console.log(data)
			// 	}
			// });
			uni.showLoading({
				title:"登录中",
				mask:true
			})
			this.$HTTP({
				url: 'login',
				data: {
					username: this.userName,
					userpass: this.userPass
				}
			}).then(loginStatus => {
				uni.hideLoading();
				if (loginStatus.data['loginState'] == 2) {
					//需要强制登陆
					uni.showModal({
						title: '是否强制登陆',
						content: loginStatus.data['message'],
						success(data) {
							if (data.confirm) {
								_this.ForceOut();
							}
						}
					});
				} else if (loginStatus.data['loginState'] == 1) {
					_this.userGuid = loginStatus.data['userGuiD'];
					_this.UserInfo();
				}
			});
		},
		ForceOut() {
			const _this = this;
			this.$HTTP({
				url: 'ForceOut',
				data: {
					userName: this.userName,
					userPass: this.userPass
				}
			}).then(ForceOutData => {
				if (ForceOutData.data['loginState'] == 1) {
					_this.$store.state.userGuid = ForceOutData.data.userGuiD;
					_this.UserInfo();
				}
			});
		},
		UserInfo() {
			const _this = this;
			this.$HTTP({
				url: 'GetUserInfo',
				data: {
					userGuid: this.userGuid,
					empid: this.userName
				}
			}).then(userInfoData => {
				if (userInfoData) {
					_this.$store.state.userInfo = userInfoData.data;
					const userInfo = userInfoData.data;
					console.log(userInfoData.data);
					uni.setStorage({
						key: 'UserEmpID',
						data: userInfoData.data['empID']
					});
					uni.setStorage({
						key: 'UserGuid',
						data: userInfoData.data['userGuid']
					});
					uni.setStorage({
						key: 'userInfo',
						data: userInfo
					});
					uni.showToast({
						title: '登陆成功',
						success() {
							uni.hideToast();
							uni.reLaunch({
								url: '../main/main'
							});
						}
					});
					// _this.StartSocket();
				} else {
					uni.showToast({
						title: '获取用户信息失败',
						icon: 'none'
					});
				}
			});
		},
		StartSocket() {
			const guid = uni.getStorageSync('UserGuid');
			uni.request({
				url: this.api + 'Socket',
				method: 'POST',
				data: {
					empId: this.userName,
					userGuid: guid
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success(data) {
					console.log(data);
				}
			});
			// console.log("diaoyongsocket")
			// ws = new WebSocket("ws://pmser.szratetec.com:8865/");
			// ws.onopen = function() {
			//     ws.send('websocekt测试');
			// };
			// ws.onmessage = function(e) {
			// 	console.log(e.data);
			//     alert("收到服务端的消息：" + e.data);
			// };
		}
	}
};
